#skills {
  background-color: var(--section-bg-color-secondary);
  color: var(--section-color-secondary);
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

#skills h1 {
  text-align: center;
  text-transform: uppercase;
  margin: 0 20vw;
  padding: .5rem;
}

#skill-list {
  padding: 1rem 2rem;
}

#skill-list .skill-column {
  margin-bottom: 1rem;
}

#skill-list h3 {
  text-align: center;
  text-transform: uppercase;
  font-style: italic;
}

.skill-category-list {
  flex-flow: wrap;
  display: flex;
}

.skill {
  height: 2rem;
  width: 50%;
  font-size: 1rem;
}

@media only screen and (min-width: 768px) {
  #skill-list .skill-column {
    margin-bottom: 5rem;
  }

  #skill-list h3 {
    text-align: left;
    padding: .5rem 0;
  }

  .skill {
    height: auto;
    padding: .5rem 0;
    font-size: 1.5rem;
  }
}

